<template>
  <transition name="fade">
    <Modal
      v-if="isModalOpen"
      :room="oneRooms[clickedNumber]"
      @closeModal="isModalOpen = false"
    />
  </transition>

  <div class="menu">
    <a v-for="menu in menus" :key="menu"> {{ menu }}</a>
  </div>

  <div class="sorting">
    <button @click="priceSort">가격순 정렬</button>
    <button @click="sortBack">되돌리기</button>
  </div>

  <Card
    @openModal="
      isModalOpen = true;
      clickedNumber = $event;
    "
    v-for="(room, i) in oneRooms"
    :key="room.title"
    :room="oneRooms[i]"
    @report="report(i)"
    :reportCount="reportCount[i]"
  />
</template>




<script>
import oneRooms from "./oneRoom.js";
import Modal from "./components/Modal.vue";
import Card from "./components/Card.vue";

export default {
  name: "App",
  data() {
    return {
      originalRooms: [...oneRooms],
      oneRooms,
      isModalOpen: false,
      menus: ["Home", "Shop", "About"],
      reportCount: [0, 0, 0, 0, 0, 0],
      clickedNumber: 0,
    };
  },

  methods: {
    report(index) {
      this.reportCount[index] = this.reportCount[index] + 1;
    },
    priceSort() {
      this.oneRooms.sort((a, b) => a.price - b.price);
    },
    sortBack() {
      this.oneRooms = [...this.originalRooms];
    },
  },
  components: {
    Modal,
    Card,
  },
};
</script>



