<template>
  <div class="black-bg">
    <div class="white-bg">
      <h4>{{ room.title }}</h4>
      <p>{{ room.content }}</p>
      <img :src="room.image" alt="room0" class="roomImage" />
      <p>{{ month }} 개월 선택 :{{ room.price * month }}원</p>
      <p class="discount">할인: {{ discount }} %</p>
      (1초에 1%씩 감소합니다)
      <p class="input-box"><input v-model="month" /> 개월 임대</p>
      <button @click="$emit('closeModal')" class="closeButton">닫기</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "Modal",

  data() {
    return {
      month: 3,
      discount: 30,
    };
  },

  watch: {
    month(num) {
      if (num > 13) {
        alert("13개월보다 더 못삼");
        this.month = 1;
      } else if (isNaN(num)) {
        alert("숫자로 입력하셈");
        this.month = 1;
      }
    },
  },

  props: {
    room: Object,
  },

  created() {
    let timer = setInterval(() => {
      this.discount--;
    }, 1000);

    setTimeout(() => {
      clearInterval(timer);
    }, 30000);
  },
};
</script>

<style>
.black-bg {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  padding: 20px;
}

.white-bg {
  width: 100%;
  background-color: white;
  border-radius: 8px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

button {
  background-color: brown;
  color: white;
}

button:hover,
h4:hover {
  cursor: pointer;
}

.input-box {
  margin-bottom: 20px;
}

.roomImage {
  width: 60%;
  margin-top: 40px;
}

.discount {
  font-size: 1.5rem;
  font-weight: 700;
  margin: 0;
}
</style>